<template>
  <div>
    <h1>Login Form</h1>
    <hr />
    <form>
      <v-text-field
        v-model="email"
        :error-messages="emailErrors"
        label="E-mail"
        required
        @input="$v.email.$touch()"
        @blur="$v.email.$touch()"
      ></v-text-field>
      <v-text-field
        type="password"
        v-model="password"
        :error-messages="passwordErrors"
        label="Password"
        required
        @input="$v.password.$touch()"
        @blur="$v.password.$touch()"
      ></v-text-field>
      <v-btn class="mr-4" @click="submit">
        submit
      </v-btn>
      <v-btn @click="clear">
        clear
      </v-btn>
    </form>
    <v-dialog v-model="dialog" max-width="400">
      <v-card>
        <v-card-title class="headline">
          User Login Status
        </v-card-title>

        <v-card-text>
          {{ serverSideMessage }}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green darken-1" text @click="dialog = false">
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { validationMixin } from 'vuelidate'
import { required, email, minLength } from 'vuelidate/lib/validators'
import { eventBus } from '../../main'
export default {
  mixins: [validationMixin],

  validations: {
    email: { required, email },
    password: { required, minLength: minLength(10) }
  },

  data: () => ({
    email: '',
    password: '',
    fetchedUsersData: [],
    usersDataAPI:
      'https://vue-training-3f5e1-default-rtdb.firebaseio.com/data.json',
    dialog: false,
    serverSideMessage: '',
    loginUserID: ''
  }),

  computed: {
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
    passwordErrors () {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.minLength &&
        errors.push('Password must be at least 10 characters long')
      !this.$v.password.required && errors.push('Password is required')
      return errors
    }
  },
  created () {
    this.axios
      .get(this.usersDataAPI)
      .then((response) => {
        const fetchedUsersData = []
        for (const key in response.data) {
          fetchedUsersData.push(response.data[key])
        }
        return fetchedUsersData
      })
      .then((processedData) => {
        this.fetchedUsersData = processedData
      })
      .catch((err) => {
        console.log(err)
      })
    localStorage.getItem('UserID')
      ? eventBus.$emit('isUserSessionActive', true)
      : eventBus.$emit('isUserSessionActive', false)
  },
  beforeRouteEnter (from, to, next) {
    localStorage.getItem('UserID') ? next('/home') : next(true)
  },
  methods: {
    submit () {
      this.$v.$touch()

      if (!this.$v.$invalid) {
        var isUserAlreadyExist = false
        for (const key in this.fetchedUsersData) {
          if (
            this.fetchedUsersData[key].email === this.email &&
            this.fetchedUsersData[key].password === this.password
          ) {
            this.loginUserID = key
            isUserAlreadyExist = true
            break
          }
        }
        if (!isUserAlreadyExist) {
          this.serverSideMessage =
            'No user found with given email and password'
          this.dialog = true
        } else {
          localStorage.setItem('UserID', this.loginUserID)
          eventBus.$emit('isUserSessionActive', true)
          this.$router.push({ path: '/dashboard' })
        }
      }
    },
    clear () {
      this.$v.$reset()
      this.email = ''
      this.password = ''
    }
  }
}
</script>
<style scoped>
form {
  margin: 5% 10% 0 10%;
}
h1 {
  text-align: center;
  margin-top: 3%;
}
</style>
