<template>
  <div>
    <h1>Registration Form</h1>
    <hr />
    <form>
      <v-text-field
        v-model="name"
        :error-messages="nameErrors"
        :counter="10"
        label="Name"
        required
        @input="$v.name.$touch()"
        @blur="$v.name.$touch()"
      ></v-text-field>
      <v-text-field
        v-model="email"
        :error-messages="emailErrors"
        label="E-mail"
        required
        @input="$v.email.$touch()"
        @blur="$v.email.$touch()"
      ></v-text-field>
      <v-text-field
        type="password"
        v-model="password"
        :error-messages="passwordErrors"
        label="Password"
        required
        @input="$v.password.$touch()"
        @blur="$v.password.$touch()"
      ></v-text-field>
      <v-select
        v-model="language"
        :items="languages"
        :error-messages="languageErrors"
        label="Favourite Language"
        required
        @change="$v.language.$touch()"
        @blur="$v.language.$touch()"
      ></v-select>
      <v-checkbox
        v-model="checkbox"
        :error-messages="checkboxErrors"
        label="Do you agree? "
        required
        @change="$v.checkbox.$touch()"
        @blur="$v.checkbox.$touch()"
      ></v-checkbox>

      <v-btn class="mr-4" @click="submit">
        submit
      </v-btn>
      <v-btn @click="clear">
        clear
      </v-btn>
    </form>
    <v-dialog v-model="dialog" max-width="400">
      <v-card>
        <v-card-title class="headline">
          Users Registration Status
        </v-card-title>

        <v-card-text>
          {{ serverSideMessage }}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green darken-1" text @click="dialog = false">
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { validationMixin } from 'vuelidate'
import {
  required,
  maxLength,
  email,
  minLength
} from 'vuelidate/lib/validators'
import { eventBus } from '../../main'

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    language: { required },
    checkbox: {
      checked (val) {
        return val
      }
    },
    password: { required, minLength: minLength(10) }
  },
  data: () => ({
    name: '',
    email: '',
    language: null,
    languages: ['C++', 'Python', 'Javascript', 'Java'],
    checkbox: false,
    password: '',
    fetchedUsersData: [],
    usersDataAPI:
      'https://vue-training-3f5e1-default-rtdb.firebaseio.com/data.json',
    dialog: false,
    serverSideMessage: ''
  }),

  computed: {
    checkboxErrors () {
      const errors = []
      if (!this.$v.checkbox.$dirty) return errors
      !this.$v.checkbox.checked && errors.push('You must agree to continue!')
      return errors
    },
    languageErrors () {
      const errors = []
      if (!this.$v.language.$dirty) return errors
      !this.$v.language.required && errors.push('Language is required')
      return errors
    },
    nameErrors () {
      const errors = []
      if (!this.$v.name.$dirty) return errors
      !this.$v.name.maxLength &&
        errors.push('Name must be at most 10 characters long')
      !this.$v.name.required && errors.push('Name is required.')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
    passwordErrors () {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.minLength &&
        errors.push('Password must be at least 10 characters long')
      !this.$v.password.required && errors.push('Password is required')
      return errors
    }
  },
  beforeRouteEnter (from, to, next) {
    localStorage.getItem('UserID') ? next('/home') : next(true)
  },
  created () {
    this.axios
      .get(this.usersDataAPI)
      .then((response) => {
        const fetchedUsersData = []
        for (const key in response.data) {
          fetchedUsersData.push(response.data[key])
        }
        return fetchedUsersData
      })
      .then((processedData) => {
        this.fetchedUsersData = processedData
      })
      .catch((err) => {
        console.log(err)
      })
    localStorage.getItem('UserID')
      ? eventBus.$emit('isUserSessionActive', true)
      : eventBus.$emit('isUserSessionActive', false)
  },
  methods: {
    submit () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        var isUserAlreadyExist = false
        for (const key in this.fetchedUsersData) {
          if (this.fetchedUsersData[key].email === this.email) {
            isUserAlreadyExist = true
            break
          }
        }

        if (isUserAlreadyExist) {
          this.serverSideMessage = 'User with given email already exist'
          this.dialog = true
        } else {
          const userData = {
            id: this.fetchedUsersData.length + 1,
            name: this.name,
            email: this.email,
            password: this.password,
            language: this.language
          }
          this.axios
            .post(this.usersDataAPI, userData)
            .then((data) => {
              if (data.status === 200) {
                this.fetchedUsersData.push(userData)
                this.serverSideMessage = 'User Registered Successfully'
                this.dialog = true
              } else {
                this.serverSideMessage =
                  'Some error occured please try later !'
                this.dialog = true
              }
            })
            .catch(() => {
              this.serverSideMessage = 'Some error occured please try later !'
              this.dialog = true
            })
        }
      }
    },
    clear () {
      this.$v.$reset()
      this.name = ''
      this.email = ''
      this.language = null
      this.checkbox = false
      this.password = ''
    }
  }
}
</script>
<style scoped>
form {
  margin: 5% 10% 0 10%;
}
h1 {
  text-align: center;
  margin-top: 3%;
}
</style>
